(function() {
  'use strict';

  angular
    .module('websnap')
    .directive('listmenu', listmenu);

  /** @ngInject */
  function listmenu() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/listmenu/listmenu.html',
      scope: {
          creationDate: '='
      },
      controller: ListMenuController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ListMenuController($mdDialog, $document, ContactService) {
      var vm = this;
      
      vm.messageModal = function(ev) {
        $mdDialog.show({
          controller: function($mdDialog, $scope, $document) {

          var querySearch = function(criteria) {
            cachedQuery = cachedQuery || criteria;
            return cachedQuery ? $scope.allContacts.filter(createFilterFor(cachedQuery)) : [];
          }

          var createFilterFor = function(query) {
            var lowercaseQuery = angular.lowercase(query);

            return function filterFn(contact) {
              return (contact._lowername.indexOf(lowercaseQuery) != -1);
            };

          }

          var cachedQuery;
          var canvas = $document.find('#display-canvas');

          $scope.allContacts = ContactService.getContacts();
          $scope.contacts = [];
          $scope.filterSelected = true;

          $scope.querySearch = querySearch;

          $scope.cancel = function() {
           $mdDialog.cancel();
          };

          $scope.send = function() {
           $mdDialog.hide($scope.msg);
          };
          },
          templateUrl: 'app/components/listmenu/messageModal.html',
          parent: angular.element($document.body),
          targetEvent: ev,
          clickOutsideToClose:false,
          fullscreen: true
        })
        .then(function(msg) {
          //Here is to call the api send message
        }, function() {
          //Exit part of the modal
        });
      };

      vm.hover = function() {
        vm.isOpen = true;
      };
      vm.notHover = function() {
        vm.isOpen = false;
      };
      
      vm.isOpen = false;
      vm.mode = 'md-scale';
      vm.options = [
        {
          name: "Contacts",
          iconURL: "assets/images/contact.svg"
        },
        {
          name: "Profile",
          iconURL: "assets/images/profile.svg"
        }
      ];
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('websnap')
    .directive('chatbox', chatbox);

  /** @ngInject */
  function chatbox() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/chatbox/chatbox.html',
      scope: {
          creationDate: '='
      },
      controller: ChatBoxController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ChatBoxController($mdDialog, $document, ChatService) {
      var vm = this;
      vm.options = ChatService.getChats();
        vm.expand = function(from) {
          $mdDialog.show({
            controller: function($mdDialog, $scope, $interval, ChatService) {
              $scope.from = from;
              $scope.index = 0;
              $scope.chats = ChatService.getChatsByName(from).content;
              $scope.progress = 0; 
              
              $scope.cancel = function() {
               //TODO: should delete the stuff from the back end here
               for (var i = $scope.chats.length - 1; i >= 0; i--) {
                 $scope.chats[i].progress = 0;
               }
               if (angular.isDefined(promise)) {
                  $interval.cancel(promise);
                  promise = undefined;
                }
               $mdDialog.cancel();
              };

              $scope.next = function() {
               $scope.chats[$scope.index].progress = 100;
               $scope.index++;
               if ($scope.index == $scope.chats.length)
                    $scope.cancel();
              };

              var promise = $interval(function() {
                $scope.chats[$scope.index].progress += 0.67;
                  if ($scope.chats[$scope.index].progress >= 100)
                    $scope.next();
              }, 100, $scope.chats.length*150, true);
            },
            templateUrl: 'app/components/chatbox/viewModal.html',
            parent: angular.element($document.body),
            clickOutsideToClose:false,
            fullscreen: true
          })
          .then(function(msg) {
            //Here is to call the api send message
          }, function() {
            //Exit part of the modal
          });
        };
      }
    }
})();

'use strict';

angular
  .module('websnap')
  .factory('SocketFactory', function($rootScope) {
    var url = 'http://localhost:3005';

    var socket = io.connect(url);
    return {
      id: 0,
      on: function(eventName, callback) {
        socket.on(eventName, function() {
          var args = arguments;
          $rootScope.$apply(function() {
            callback.apply(socket, args);
          });
        });
      },
      emit: function(eventName, data, callback) {
        socket.emit(eventName, data, function() {
          var args = arguments;
          $rootScope.$apply(function() {
            if (callback) {
              callback.apply(socket, args);
            }
          });
        });
      }
    };
  });

(function() {
  'use strict';

  angular
    .module('websnap')
    .service('ContactService', function(){
        var contacts = [
              'John Smith',
              'Mike Bay',
              'Stan Zeng',
              'James Bond',
              'Chris Prat'
            ];

        this.getContacts = function() {
          return contacts.map(function (c) {
              var cParts = c.split(' ');
              var contact = {
                name: c,
                email: cParts[0][0].toLowerCase() + '.' + cParts[1].toLowerCase() + '@example.com'
              };
              contact._lowername = contact.name.toLowerCase();
              return contact;
            });
        };
     
    });

})();


